.formContainer {
    margin: 3vw 3vw;
    padding: 3vw 13vw;
    box-shadow: rgba(175, 175, 250, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    background-color: #fff;

}

.formContainer form {
    text-align: left;

}

.formContainer form input, select {
    margin: 0vw 0vw 0.75vw 0vw;
    width: 100%;
    border: 1px solid #ddd;
    padding: 0.2vw 0;
    text-align: left;
}

.formContainer form input:focus, select:focus {
    outline: none
}

.formContainer form button {
    border: none;
    background-color: #1976d2;
    color: #fff;
    padding: 0.25vw;
    margin-top: 1vw;
    width: 100%;
    font-size: calc(4px + 1vw);
}

.signup {
    margin-top: 3vw;
}

.message {
    color: #e33371
}


