@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: 'Noto Sans KR', sans-serif;
}

/* BUTTONS */


.closeButton {
  background-color: #d32f2f;
  margin-top: 2vw;
}

.closeButton:active {
  background-color: #e33371;
  margin-top: 2vw;
}


.editButton {
  background-color: #f57c00;
}

.editButton:active {
  background-color: #ffb74d;

}

.logOut {
  background-color: #d32f2f;
}

.logOut:active {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  background-color: #e33371;
}

button {
  background-color: #1976d2;
  border: none;
  color: #fff;
  padding: 0.25vw;
  width: 33vw;
  font-size: calc(4px + 1vw);
  justify-self: center;
}

button:active {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  background-color: #4791db
}
.navbar {
    background-color: #115293;
    color: #fff;
    font-size: calc(8px + 1vw);
    font-weight: 200;
    margin: 2vw 0;

}


.navbar ul {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding: 1vw 10vw;

}

@media only screen and (max-width: 600px) {
    .navbar ul {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        padding: 1vw 3vw;

    }
  }

.navbar ul li {
    list-style: none;
    padding: 1vw 0;

}

.navbar ul a {
    text-decoration: none;
}

.navbar ul a:visited {
    text-decoration: none; 
    color: #fff;

}
.tableContainer {
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 3vw;
    margin: 3vw;
    background-color: #fff;
    box-shadow: rgba(175, 175, 250, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

}

.table {
    margin: 3vw;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;    
    display: grid;
    justify-self: center;
    font-size: calc(7px + 1vw);
    min-width: 67vw;

}



.header{
    padding: 0.5vw;
    background-color: #115293;
}



.headerCell {
    padding: 0 0.5vw;
    color: #f2f2f2;

}

/* Date Header */
.dateHeader:hover {
    cursor: default
}


.cell {
    padding: 0 0.5vw;
    border: 1px solid #ddd;
}

.date {
    font-size: calc(3px + 1vw);
}

.row:nth-child(even) {background-color: #f2f2f2;}

.row:hover {
    background-color: #ddd;
    cursor: default;
}




/* Event */
.eventHeader {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 3.5fr 1fr 1fr;
}

.eventRow {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 3.5fr 1fr 1fr;
}

/* Iventory */

.inventoryHeader {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;

}

.inventoryRow {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;
}


/* Inventory Selected */

.inventorySelectedHeader {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr ;
}

.inventorySelectedRow {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr ;
}

/* Inventory  Events  */

.inventoryEventsHeader {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr ;

}

.inventoryEventsRow {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr ;
}

/* Desc  */

.descHeader{
    display: grid;
    grid-template-columns: 1fr;
}

.descRow {
    display: grid;
    grid-template-columns: 1fr;
    padding: 0.5vw;
    font-size: calc(7px + 0.75vw);
}

.descCell {
    padding: 1vw;
}

.descCell h4, p {
    font-weight: 300;
}

.descCell h3 {
    margin-top: 2vw;
}

.descCell span {
    padding-top: 10vw;
}


.tableContainer tr:nth-child(even){background-color: #f2f2f2;}

.tableContainer tr:hover {background-color: #ddd;}

